<template>
  <div class="register">
    <mu-appbar z-depth="2" style="width: 100%;" color="primary">
	  <mu-button icon slot="left" @click="goBack">
	    <i class="icon iconfont" style="font-size: 22px">&#xe647;</i>
	  </mu-button>
  		创建钱包
	  <mu-button flat slot="right"></mu-button>
	</mu-appbar>
		
		<div style="display: flex;align-items: center;justify-content:center">
			<p style="width: 40px;height:40px;border-radius: 50%;background-color: #E5E5E5;text-align: center;line-height: 40px;"><span :class="activeStep==1 ? 'active-step':''"><span v-if="activeStep==1">1</span><i class="iconfont" style="color:white;font-size: 26px;" v-if="activeStep>=2">&#xe6a7;</i></span></p>
			<p style="align-items: center;width: 25%;height:20px;margin-left:-1%;background-color: #E5E5E5;">
				<p v-if="activeStep>=2" :class="activeStep>=2 ? 'active-line':''"></p>
			</p>
			<p style="width: 40px;height:40px;border-radius: 50%;background-color: #E5E5E5;margin-left:-2%;text-align: center;line-height: 40px;"><span :class="activeStep==2 ? 'active-step':''"><span v-if="activeStep<=2">2</span><i class="iconfont" style="color:white;font-size: 26px;" v-if="activeStep>=3">&#xe6a7;</i></span></p>
			<p style="align-items: center;width: 25%;height:20px;margin-left:-1%;background-color: #E5E5E5;">
				<p v-if="activeStep>=3" :class="activeStep>=3 ? 'active-line':''"></p>
			</p>
			<p style="width: 40px;height:40px;border-radius: 50%;background-color: #E5E5E5;margin-left:-2%;text-align: center;line-height: 40px;"><span v-if="activeStep<3">3</span><i class="iconfont" style="color:white;font-size: 26px;" v-if="activeStep==3">&#xe6a7;</i></p>
		</div>
		<div v-if="activeStep==1" style="padding: 20px 10%;">
			<p style="color: #b5b5b5;">设置钱包名称</p>
			<input placeholder="请输入钱包名称" v-model="walletName" style="padding-left: 10px;height: 47px;width: 100%;border: 1px solid #e5e5e5;background-color: transparent;"/>
			<mu-button full-width style="height: 44px;margin-top: 30px;" @click="oneStep" color="#295DF1">下一步</mu-button>
		</div>
		<div v-if="activeStep==2" style="padding: 20px 10%;">
			<p style="color: #b5b5b5;">设置交易密码</p>
			<deal-password v-model="password"></deal-password>
			<p style="color: #b5b5b5;margin-top: 20px;">确认交易密码</p>
			<deal-password v-model="dealPasswordAgain"></deal-password>
			<mu-button full-width style="height: 44px;margin-top: 30px;" @click="twoStep" color="#295DF1">下一步</mu-button>
		</div>
		<div v-if="activeStep==3" style="padding: 20px 10%;">
			<div style="text-align: center;margin-top: 20px;">
				<i class="iconfont" style="font-size:60px;color: #295DF1;">&#xe669;</i>
				<p style="margin: 1px 0">钱包创建成功 !</p>
			</div>
			<p style="line-height:1.4;padding:8px;font-size: 14px;background: #e5e5e5;color:#838383;border-radius: 5px;"> 
				注意：拥有钱包私匙就能安全控制钱包资产，因此强烈建议你在使用钱包前做好备份，将钱包私匙保存到安全的地方.建议备份完成后立即恢复一次，以确保安全。然后尝试转入，转出小额资产开始使用。
			</p>
			<router-link to="/home"><mu-button full-width style="height: 44px;margin-top: 30px;" color="#295DF1">进入钱包</mu-button></router-link>
		</div>
		</div>
  </div>
</template>
<script>
import DealPassword from '@/components/dealPassword'
let web3 = null;
export default {
  name: "Regist",
  components:{
  	'deal-password':DealPassword
  },
  data() {
    return {
      activeStep:1,
      walletName:'Transfer',
      password:'',
      dealPasswordAgain:'',
    };
  },
  computed: {
     
  },
  methods: {
  	goBack(){
  		this.$router.back();
  	},
		Login(){
			this.$router.push('/wallet')
		},
		//第一步
		oneStep(){
			if(!this.walletName){
				this.$layer.open({
					content: '请输入钱包名称'
			    ,skin: 'msg'
			    ,time: 2 //2秒后自动关闭
				})
				return
			}
			this.activeStep=2;
		},
		//第二步
		twoStep(){
			if(this.password==''){
				this.$layer.open({
					content: '请输入交易密码'
			    ,skin: 'msg'
			    ,time: 2 //2秒后自动关闭
				})
				return
			}else if(this.dealPasswordAgain==''){
				this.$layer.open({
					content: '请确认交易密码'
			    ,skin: 'msg'
			    ,time: 2 //2秒后自动关闭
				})
				return
			}else if(this.dealPasswordAgain!=this.password){
				this.$layer.open({
					content: '两次密码输入不一致'
			    ,skin: 'msg'
			    ,time: 2 //2秒后自动关闭
				})
				return
			}else{
				this.creatWallet();
			}
		},
		creatWallet() {
			let _this = this;
			let ajaxLoading = _this.$layer.open({
					type: 2,
					content: '钱包创建中...'
			})
			let walletInfo = {};
			let localStorageWalletArr=[];
      let secretSeed = lightwallet.keystore.generateRandomSeed(); // 生成助记词
      console.log("助记词:", secretSeed); // 助记词
      let address = ""; // 地址
      let privateKey = ""; // 私钥
      let  hdPathString = "m/44'/60'/0'/0";
      lightwallet.keystore.createVault(
        {
          password: _this.dealPasswordAgain,
          seedPhrase: secretSeed,
          hdPathString: hdPathString
        },function(err, ks) {
          ks.keyFromPassword(_this.dealPasswordAgain, function(err, pwDerivedKey) {
            _this.$layer.close(ajaxLoading);
            if (err) throw err;
            ks.generateNewAddress(pwDerivedKey, 1);
            address = ks.getAddresses()[0];
            privateKey = ks.exportPrivateKey(address, pwDerivedKey);
            console.log("地址:", address); // 地址
            console.log("私钥:", privateKey); // 私钥
//          console.log(web3.eth.getBalance(address));//余额
						_this.activeStep=3;
//          web3 = new Web3(new Web3.providers.HttpProvider("http://192.168.0.136:8080"));
						if(localStorage.getItem('walletArr')==''||localStorage.getItem('walletArr')=='undefined'||localStorage.getItem('walletArr')==null){
							walletInfo={
								walletName:_this.walletName,
								secretSeed:secretSeed,
								address:address,
								privateKey:privateKey,
								pwd:_this.dealPasswordAgain,
								isUse:true,
							}
							localStorage.setItem('chooseWallet',JSON.stringify(walletInfo));
						}else{
						  localStorageWalletArr = JSON.parse(localStorage.getItem('walletArr'));
							walletInfo={
								walletName:_this.walletName,
								secretSeed:secretSeed,
								address:address,
								privateKey:privateKey,
								pwd:_this.dealPasswordAgain,
								isUse:false,
							}
						}
						localStorageWalletArr.push(walletInfo)
						localStorage.setItem('walletArr',JSON.stringify(localStorageWalletArr))
          });
        }
      );
    },
  },
  
  computed: {
    
  },
  watch:{
  	dealPasswordAgain(newV,oldV){
  		if(newV.length == 6){
	  		if(newV!=this.password){
	  			this.$layer.open({
						content: '两次密码输入不一致'
					    ,skin: 'msg'
					    ,time: 2 //2秒后自动关闭
					})
	  			this.dealPasswordAgain=''
	  		}
  		}
  	}
  }
};
</script>
<style scoped>
	.active-step{
		background-color: white;padding: 5px 9px;border-radius: 50%;
	}
	.active-line{
		position: relative;z-index:10;height: 8px;background: white;width: 27%;margin-left: -26%;
	}
</style>




